<template>
  <div>
    <div class="d-flex">
      <v-icon>mdi-card-text</v-icon>
      <div class="title font-weight-bold ml-3">
        Judul
      </div>
    </div>            
    <v-text-field
      v-model="changeData"      
      dense
      @input="showBtn = true"
      class="mt-2 ml-9"      
    ></v-text-field> 
    <div class="d-flex">
      <v-spacer></v-spacer>  
      <v-btn 
        v-if="showBtn"
        depressed color="primary"
        :loading="isLoading"
        @click="updateData"
        small>
        simpan
      </v-btn>
    </div> 
  </div>
</template>

<script>
  import {
    putDataCardName,
  } from '@/config/startup/sprint'

export default {
  props: ['data', 'id'],

  data: () => ({
    showBtn: false,
    title: '',
    isLoading: false
  }),

  computed: {
    changeData: {
      get() {
        return this.data
      },
      set(val){
        this.title = val
      }
    }
  },

  methods: {
    updateData () {
      this.isLoading = true
      putDataCardName(this.id, {
        title: this.title
      })
      .then(res => {
        if (res.data.status === 'success') {
          this.$emit('visible', 'success')
          this.$store.dispatch('dataStartupSprint/getDataList', this.$route.params.sprint_id)
          this.isLoading = false
          this.showBtn = false
        } else {
          this.$emit('visible', 'error')
          this.isLoading = false
          this.showBtn = false
        }
      })
      .catch(() => {
        this.$emit('visible', 'error')
        this.isLoading = false
        this.showBtn = false
      })      
    },
    handleClickOutside(evt) {
      if (!this.$el.contains(evt.target)) {
        this.showBtn = false;
      }
    }
  },
  mounted() {
    document.addEventListener('click', this.handleClickOutside);
  },
  destroyed() {
    document.removeEventListener('click', this.handleClickOutside);
  },
}
</script>