<template>
  <div>
    <div class="d-flex">
      <v-icon>mdi-attachment</v-icon>
      <div class="title font-weight-bold ml-3 mb-3">
        Attachment
      </div>
    </div>
    <div v-if="cekFile(data) === ''"></div>                 
    <div 
      v-if="cekFile(data) === 'gambar'" 
      class="d-flex justify-center align-center">
      <img              
        :src="`https://progresio.id/api/public/uploads/berkas/${data}`" 
        width="150" 
      /> <br>
      <v-btn
        text
        small
        class="ml-3"
        @click="download(data)"
        color="primary">
        download
      </v-btn>
    </div>  
    <div 
      v-if="cekFile(data) === 'file'"
      class="d-flex align-center justify-center my-4">
      <v-icon class="mr-3">mdi-file</v-icon>
      <span>{{ data }}</span>
      <v-btn
        text
        small
        @click="download(data)"
        class="ml-3"
        color="primary">
        download
      </v-btn>
    </div>          
    <v-file-input
      show-size
      counter
      label="File"
      class="ml-9"
      v-model="file"
    ></v-file-input>
    <div class="d-flex">
      <v-spacer></v-spacer>  
      <v-btn 
        v-if="showBtn"
        class="mt-3"
        :lodaing="isLoading"
        depressed color="primary"
        @click="updateData"
        small>
        simpan
      </v-btn>
    </div> 
  </div>
</template>

<script>
  import {
    putDataCardFile,
  } from '@/config/startup/sprint'
  import { saveAs } from 'file-saver'

export default {
  props: ['data', 'id'],

  data: () => ({
    showBtn: false,
    file: null,
    isLoading: false
  }),

  computed: {
    changeData: {
      get() {
        return this.data
      },
      set(val){
        this.file = val
      }
    }
  },

  watch: {
    file() {
      this.showBtn = true
    }
  },

  methods: {
    cekFile(val) {
      if (val == null) {
        return ''
      }
      const file = val.split('.')
      const jenisFile = file[1]
      if (jenisFile === 'jpeg') {
        return 'gambar'
      } else {
        return 'file'
      }
    },
    updateData () {
      this.isLoading = true
      const data = new FormData()
      data.append('berkas', this.file)
      putDataCardFile(this.id, data)    
      .then(res => {
        if (res.data.status === 'success') {
          this.$emit('visible', 'success')
          this.$store.dispatch('dataStartupSprint/getDataList', this.$route.params.sprint_id)
          this.isLoading = false
          this.showBtn = false
        } else {
          this.$emit('visible', 'error')
          this.isLoading = false
          this.showBtn = false
        }
      })
      .catch(() => {
        this.$emit('visible', 'error')
        this.isLoading = false
        this.showBtn = false
      })
    },
    handleClickOutside(evt) {
      if (!this.$el.contains(evt.target)) {
        this.showBtn = false;
      }
    },
    download(nama){
      const format = nama.split('.')
      saveAs(`https://progresio.id/api/public/uploads/berkas/${nama}`, 'berkas_card.'+format[1])
    }
  },
  mounted() {
    document.addEventListener('click', this.handleClickOutside);
  },
  destroyed() {
    document.removeEventListener('click', this.handleClickOutside);
  }
}
</script>