<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn
        text v-on="on"
        block
        color="cyan">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        tambah kegiatan
      </v-btn>
    </template>

    <v-card>
      <div class="pa-2 text-center">
        <span class="title">Tambah Kegiatan</span>
      </div>
      <v-form v-model="valid" ref="form" enctype="multipart/form-data">
        <v-card-text>
          <v-text-field
            v-model="data.title"
            label="Nama"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>    
          <!-- <v-file-input
            accept="image/*"
            v-model="dataKegiatan.gambar"
            show-size
            counter
            label="Gambar"
          ></v-file-input> -->   
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoadingCard"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    props: ['card', 'id'],

    data () {
      return {
        dialog: false,
        valid: true,
        data: {
          title: '',
          list_id: ''
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    computed: {
      ...mapState('dataStartupSprint', ['isLoadingCard', 'statusCard']),      
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.data.list_id = this.id
          this.data.urut = this.card.length+1
          this.data.board_id = this.$route.params.sprint_id
          this.$store.dispatch('dataStartupSprint/postDataCard', this.data)
          this.$store.dispatch('dataStartupSprint/updateIsLoadingCard', true)
        }
      }
    }
  }
</script>