<template>
  <div>
    <div class="d-flex">
      <v-icon>mdi-text</v-icon>
      <div class="title font-weight-bold ml-3">
        Deskripsi
      </div>
    </div>            
    <v-textarea
      class="mt-2 ml-9"
      outlined
      full-width
      v-model="changeData"
      @input="showBtn=true"
    ></v-textarea>
    <div class="d-flex">
      <v-spacer></v-spacer>  
      <v-btn 
        v-if="showBtn"
        :loading="isLoading"
        depressed color="primary"
        @click="updateData"
        small>
        simpan
      </v-btn>
    </div> 
  </div>
</template>

<script>
  import {
    putDataCardDesc,
  } from '@/config/startup/sprint'

export default {
  props: ['data', 'id'],

  data: () => ({
    showBtn: false,
    desc: '',
    isLoading: false
  }),

  computed: {
    changeData: {
      get() {
        return this.data
      },
      set(val){
        this.desc = val
      }
    }
  },

  methods: {
    updateData () {
      this.isLoading = true
      putDataCardDesc(this.id, {
        description: this.desc
      })
      .then(res => {
        if (res.data.status === 'success') {
          this.$emit('visible', 'success')
          this.$store.dispatch('dataStartupSprint/getDataList', this.$route.params.sprint_id)
          this.isLoading = false
          this.showBtn = false
        } else {
          this.$emit('visible', 'error')
          this.isLoading = false
          this.showBtn = false
        }
      })
      .catch(() => {
        this.$emit('visible', 'error')
        this.isLoading = false
        this.showBtn = false
      })
    },
    handleClickOutside(evt) {
      if (!this.$el.contains(evt.target)) {
        this.showBtn = false;
      }
    }
  },
  mounted() {
    document.addEventListener('click', this.handleClickOutside);
  },
  destroyed() {
    document.removeEventListener('click', this.handleClickOutside);
  },
}
</script>