<template>
  <v-dialog
    v-model="dialog"
    width="800">
    <template v-slot:activator="{ on }">
      <v-icon v-on="on">mdi-dots-horizontal</v-icon>
    </template>

    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="dialog = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text class="black--text">        
        <v-row>
          <v-col cols="12" md="8" sm="12">
            <EditTitle :id="detailCard.id" :data="detailCard.title" @visible="visible" />
            <EditDesc :id="detailCard.id" :data="detailCard.description" @visible="visible" />                        
            <EditFile :id="detailCard.id" :data="detailCard.berkas" @visible="visible" />                                     
          </v-col>
          <v-col cols="12" md="4" sm="12">
            <component :is="tabs" :id="data.id" :column_id="column_id" @actions="changeComponent" @dialog="closeDialog"></component>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>

    <v-snackbar
      top :color="status"
      v-model="snackbar">
      <span v-if="status === 'success'">Berhasil diubah</span>
      <span v-else>Gagal diubah</span>
    </v-snackbar>
  </v-dialog>
</template>

<script>
  import {
    getDetailCard
  } from '@/config/startup/sprint'
  import Copy from './Copy'
  import Move from './Move'
  import Actions from './Actions'
  import EditFile from "./Detail/EditFile";
  import EditDesc from "./Detail/EditDesc";
  import EditTitle from "./Detail/EditTitle";

  export default {
    props: ['data', 'column_id'],

    data () {
      return {
        dialog: false,
        valid: true,
        detailCard: {},
        tabs: 'Actions',
        snackbar: false,
        status: ''
      }
    },

    components: {
      Actions,
      Move,
      Copy,
      EditFile,
      EditDesc,
      EditTitle
    },

    watch: {
      dialog(val) {
        if (val === true) {
          getDetailCard(this.data.id)
            .then(res => this.detailCard = res.data.data[0])
        }
      }
    },

    methods: {            
      changeComponent(val) {
        this.tabs = val
      },
      closeDialog(val) {
        this.dialog = val
      },
      visible(val) {
        this.snackbar = true
        this.status = val
      }
    }
  }
</script>