<template>
	<div>
		<p class="title grey--text">ACTIONS</p>
    <v-btn
      depressed
      block
      color="grey lighten-2"
      @click="$emit('actions', 'Move')"
      class="justify-start mb-3">
      <v-icon class="mr-3">mdi-arrow-right</v-icon>
      pindah ke
    </v-btn>
    <v-btn
      depressed
      block
      color="grey lighten-2"
      @click="$emit('actions', 'Copy')"
      class="justify-start mb-3">
      <v-icon class="mr-3">mdi-content-copy</v-icon>
      salin ke
    </v-btn>           
    <!-- <v-btn
      depressed
      block
      color="grey lighten-2"
      class="justify-start mb-3">
      <v-icon class="mr-3">mdi-clock</v-icon>
      due date
    </v-btn> -->
    <v-btn
      depressed
      block
      :loading="isLoading"
      @click="hapusCard"
      color="grey lighten-2"
      class="justify-start mb-3">
      <v-icon class="mr-3">mdi-trash-can</v-icon>
      hapus
    </v-btn>
	</div>
</template>

<script>
  import { deleteDataCard } from '@/config/startup/sprint'
  export default{
    props: ['id', 'column_id'],

    data: () => ({
      isLoading: false
    }),
    
    methods: {
      hapusCard() {
        this.isLoading = true
        deleteDataCard({
          card_id: this.id,
          column_id: this.column_id
        })
          .then(() => {
            this.$store.dispatch('dataStartupSprint/getDataList', this.$route.params.sprint_id)
            this.isLoading = false
            this.$emit('dialog', false)
          })
      }
    }
  }
</script>