<template>
  <div>
    <div class="d-flex align-center">
      <v-icon class="mr-3">mdi-content-copy</v-icon>    
      <div class="title">Salin ke</div>
    </div>
    <div class="subtitle-1">Sprint</div>
    <v-select
      :items="dataSprint"
      item-text="nama"
      item-value="id"
      v-model="sprint_id"
      dense
      outlined
    ></v-select>
    <div class="subtitle-1">List</div>
    <v-select
      :disbaled="sprint_id===''?true:false"
      :items="dataList"
      item-text="nama"
      item-value="id"
      v-model="list_id"
      dense
      outlined
    ></v-select>
    <div class="d-flex justify-end">
      <v-btn
        text small
        @click="$emit('actions', 'Actions')"
        color="primary">
        kembali
      </v-btn>
      <v-btn
        depressed small
        @click="copyCard"
        :loading="isLoading"
        color="primary">
        salin
      </v-btn>
    </div>
  </div>
</template>

<script>
  import { copyDataCard, getDataList } from '@/config/startup/sprint'
  import { mapState } from 'vuex'

  export default {
    props: ['id'],

    data: () => ({
      sprint_id: '',
      isLoading: false,
      dataList: [],
      list_id: 0
    }),

    computed: {    
      ...mapState('dataStartupSprint', ['dataSprint']),     
    },

    watch: {
      sprint_id(val) {
        getDataList(val)
          .then(res => {
            this.dataList = res.data.data
          })   
      }
    },

    methods: {
      copyCard() {
        this.isLoading = true
        copyDataCard({
          card_id: this.id,
          // list_id: val
          list_id: this.list_id
        })
          .then(() => {
            this.$store.dispatch('dataStartupSprint/getDataList', this.$route.params.sprint_id)
            this.isLoading = false
            this.$emit('dialog', false)            
          })
      }
    }
  }
</script>