<template>
	<v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
			<v-btn
				v-on="on"
        icon
        class="white mr-3"
        small
        color="primary">
        <v-icon x-small>mdi-pencil</v-icon>
      </v-btn>
    </template>

    <v-card>
      <div class="pa-2 text-center">
        <span class="title">Edit List</span>
      </div>
      <v-form v-model="valid" ref="form">
        <v-card-text>
          <v-text-field
            v-model="data.name"
            label="Nama"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field> 

          <Notif :msg="msg" :status="statusList" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoadingList"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        modal: false,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Berhasil diubah',
          error: 'Gagal diubah',
          visible: false
        }
      }
    },

    components: {
      Notif
    },

    computed: {
      ...mapState('dataStartupSprint', ['isLoadingList', 'statusList']),      
    },

    watch: {
      statusList(val) {
        if (val === null) this.dialog = false
      }
    }, 

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          const data = {
            id: this.data.id,
            nama: this.data.name,
            board_id: this.$route.params.sprint_id
          }
          this.$store.dispatch('dataStartupSprint/putDataList', data)
          this.$store.dispatch('dataStartupSprint/updateIsLoadingList', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataStartupSprint/updateStatusList', null)
      }
    }
  }
</script>