<template>
  <v-card>
    <v-card-title>
			<v-btn
        depressed
        color="cyan"
        id="my-canvas"
        @click="$router.push({ name: 'halaman-cetak-startup', params: {sprint_id: sprint_id} })"
        dark>
        <v-icon class="mr-2">mdi-file-pdf</v-icon>
        export ke pdf
      </v-btn>
    </v-card-title>
    <v-card-text>
  <div class="d-flex demo">
    <v-col v-if="isLoadingList" cols="12" sm="3">
      <Loader type="card" />      
    </v-col>
    <div v-else-if="scene.children.length > 0 && !isLoadingList">
      <Container
        behaviour="contain"
        orientation="horizontal"
        @drop="onColumnDrop($event)"
        drag-handle-selector=".column-drag-handle"
        @drag-start="dragStart">
        <Draggable v-for="(column, indexCard) in scene.children" :key="indexCard">
          <v-card 
            width="320" 
            class="mb-6"
            :class="{'mr-4': scene.children.length === indexCard+1?false:true}">
            <v-card-title 
              class="py-1 purple lighten-4 font-weight-regular subtitle-1 column-drag-handle">
              {{ column.name }}
              <v-spacer></v-spacer>
              <Edit :data="column" />
              <Hapus :id="column.id" />
            </v-card-title>
            <v-card-text class="pa-3 pb-0 grey lighten-3 card-container">
              <Container
                group-name="col"
                @drop="onCardDrop(column.id, $event)"
                @drag-start="ambilCard(column.id, $event)"
                drag-class="card-ghost"
                drop-class="card-ghost-drop"
                :get-child-payload="getCardPayload(column.id)"
                :drop-placeholder="dropPlaceholderOptions">
                <Draggable v-for="(card, i) in column.children" :key="i">
                  <v-sheet class="pa-3 mb-3 pb-1">  
                    <p>{{ card.data }}</p>       
                    <div class="d-flex justify-end">
                      <DetailCard :data="card" :column_id="column.id" />
                    </div>
                  </v-sheet>
                </Draggable>
              </Container>
            </v-card-text>
            <v-card-actions class="grey lighten-3">
              <TambahCard :card="column.children" :id="column.id" />
            </v-card-actions>
          </v-card>
        </Draggable>
      </Container>
    </div>
    <div :class="{'ml-4': scene.children.length > 0?true:false}">
      <v-card>
        <v-form v-model="valid" ref="form">
          <v-card-text class="pb-0">
            <v-text-field
              outlined
              :rules="requiredRule"
              v-model="dataSprintList.nama"
              dense
              autocomplete="off"
              placeholder="Nama list"
            ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-btn
              type="submit"
              :disabled="!valid"
              @click.prevent="validate"
              depressed
              width="320"
              color="cyan"
              class="white--text">
              <v-icon class="mr-3">mdi-plus-circle</v-icon>
              tambah list
            </v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </div>
  </div>
    </v-card-text>
  </v-card>  
</template>

<style>
  .demo{
    padding: 8px;
    overflow: auto;
    min-width: 0;
  }
  .card-container{
    max-height: 500px;
    overflow-y: auto; 
  }
  .column-drag-handle{ cursor: move; }
  .card-ghost {
    transition: transform 0.18s ease;
    transform: rotateZ(5deg)
  }
  .card-ghost-drop {
    transition: transform 0.18s ease-in-out;
    transform: rotateZ(0deg)
  }
  .drop-preview {
    background-color: rgba(150, 150, 200, 0.1);
    border: 1px dashed #abc;
    margin-bottom: 16px;
    /*width: 500px;*/
  }
  .cards-drop-preview {
    background-color: rgba(150, 150, 200, 0.1);
    border: 1px dashed #abc;
    margin-right: 16px;
  }
</style>

<script>
import { mapState } from 'vuex'
import { Container, Draggable } from 'vue-smooth-dnd'
import Loader from '@/components/Loader'
import TambahCard from './Card/TambahCard'
import DetailCard from './Card/DetailCard'
import Edit from './List/Edit'
import Hapus from './List/Hapus'

const scene = {
  type: 'container',
  props: {
    orientation: 'horizontal'
  },
  children: []
}
export default {
  props: ['sprint_id'],

  components: {
    Container, 
    Draggable,
    Loader,
    Hapus,
    Edit,
    DetailCard,
    TambahCard
  },

  data () {
    return {
      // scene,     
      valid: true,
      dataSprintList: {
        nama: '',
        board_id: ''
      },
      requiredRule: [v=>!!v || 'Harus di isi'], 
      upperDropPlaceholderOptions: {
        className: 'cards-drop-preview',
        animationDuration: '150',
        showOnTop: true
      },
      dropPlaceholderOptions: {
        className: 'drop-preview',
        animationDuration: '150',
        showOnTop: true
      },
      cardAsalUrut: 0,
      cardAsalId: 0,
      listAsalId: 0
    }
  },

  created() {
    this.$store.dispatch('dataStartupSprint/getDataList', this.sprint_id)
    this.$store.dispatch('dataStartupSprint/updateIsLoadingList', true)   
    this.$store.dispatch('dataStartupSprint/getDataSprint')
  },

  computed: {
    ...mapState('dataStartupSprint', [
      'dataList', 'isLoadingList', 'statusList'
    ]),
    scene() {
      scene.children = this.dataList
      return scene
    }
  },

  methods: {
    onColumnDrop (dropResult) {
      const pindah = this.dataList
        .filter((x, i) => i === dropResult.removedIndex)
      const kepindah = this.dataList
        .filter((x, i) => i === dropResult.addedIndex)
      const listPindah = {...pindah[0]}
      const listKepindah = {...kepindah[0]}
      this.$store.dispatch('dataStartupSprint/moveDataList', {
        list_pindah: listPindah.id,
        list_kepindah: listKepindah.id,
        board_id: parseInt(this.sprint_id)
      })
    },
    onCardDrop (listId, dropResult) {
      if (dropResult.removedIndex !== null && dropResult.addedIndex !== null) {
        const list = this.dataList
          .filter(list => list.id === listId)[0]

        const pindah = list.children
          .filter((x, i) => i === dropResult.removedIndex)[0]

        const kepindah = list.children
        .filter((x, i) => i === dropResult.addedIndex)[0]

        this.$store.dispatch('dataStartupSprint/moveDataCard', {
          card_pindah_id: pindah.id,
          card_kepindah_id: kepindah.id,
          board_id: parseInt(this.sprint_id)
        })     
      } else if (dropResult.removedIndex !== null || dropResult.addedIndex !== null) {
        if (dropResult.addedIndex !== null) {
          let urut = 0
          const list = this.dataList
            .filter((x) => x.id === listId)[0]
          if (list.children.length === 0) {
            urut = 1
          } else {
            urut = dropResult.addedIndex+1
          }
          const data = {
            listAsal: this.listAsalId,
            idCard: this.cardAsalId,
            urutCardAsal: this.cardAsalUrut,
            idList: listId,
            urut: urut,
            board_id: parseInt(this.sprint_id)
          }
          // console.log(data)
          this.$store.dispatch('dataStartupSprint/moveDataCardList', data)        
        }
      }
    },
    dragStart () {
      console.log('drag started')
    },
    getCardPayload (columnId) {
      return index => {
        return this.scene.children.filter(p => p.id === columnId)[0].children[index]
      }
    },
    ambilCard (listId, params) {
      this.cardAsalId = params.payload.id
      this.cardAsalUrut = params.payload.urut
      if (params.isSource) this.listAsalId = listId      
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.dataSprintList.board_id = this.sprint_id
        this.dataSprintList.urut = this.scene.children.length+1
        this.$store.dispatch('dataStartupSprint/postDataList', this.dataSprintList)
        this.$store.dispatch('dataStartupSprint/updateIsLoadingList', true)        
      }
    }
  },
}
</script>