<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on }"> 
      <v-btn
        v-on="on"
        class="white"
        icon small
        color="error">
        <v-icon x-small>mdi-trash-can</v-icon>
      </v-btn>
    </template>
    <v-card>
      <v-card-title class="title pa-6">
        Apakah anda yakin ingin menghapusnya ?
      </v-card-title>
      <v-divider></v-divider>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" text @click="dialog = false">kembali</v-btn>
        <v-btn @click="hapusData" :loading="isLoadingList" color="primary">hapus</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    props: ['id'],

    data: () => ({
      dialog: false,
    }),

    methods: {
      hapusData () {
        this.$store.dispatch('dataStartupSprint/deleteDataList', {
          id: this.id,
          board_id: parseInt(this.$route.params.sprint_id)
        })
        this.$store.dispatch('dataStartupSprint/updateIsLoadingList', true)
      }
    },

    computed: {
      ...mapState('dataStartupSprint', ['isLoadingList', 'statusList'])
    },

    watch: {
      statusList(val) {
        if (val === true) this.dialog = false
      }
    }
  }
</script>